.def-slider{
	position: relative;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-wrap: wrap;
	min-width: 0;
	/* Слайдер */
	.slick-slider{}
	/* Слайдер запущен */
	.slick-slider.slick-initialized{}
	/* Слайдер с точками */
	.slick-slider.slick-dotted{}
	/* Ограничивающая оболочка */
	.slick-list {
		flex: 100% 0 0;
		order: 0;
		height: 100%;
	}
	/* Лента слайдов */
	.slick-track{
		height: 100%;
		div{
			height: 100%;
		}
	}
	/* Слайд */
	.slick-slide{
	}
	/* Слайд активный (показывается) */
	.slick-slide.slick-active{}
	/* Слайд основной */
	.slick-slide.slick-current{}
	/* Слайд по центру */
	.slick-slide.slick-center{}
	/* Клонированный слайд */
	.slick-slide.slick-cloned{}
	/* Стрелка */
	&:not(.arrows){
		.slick-arrow{
			display: none !important;
		}
	}
	&.arrows{
		.slick-list {
		}
		.slick-arrow{
			cursor: pointer;
			font-size: 0;
			line-height: 0;
			box-sizing: border-box;
			width: 30px;
			height: 30px;
			z-index: 10;
			background-color: transparent;
			border: none;
			position: relative;
			&:before{
				display: block;
				content: '';
				width: 0;
				height: 0;
				position: absolute;
				top: 0;
				left: 0;
				border-left: 15px solid transparent;
				border-bottom: 15px solid transparent;
				border-right: 15px solid $highlightColor;
				border-top: 15px solid $highlightColor;
				transition: all 0.2s;
				
			}
			&:active{
				&:before{
					filter: brightness(0.9);
				}
			}
			// border-radius:8%;
		}
		.slick-arrow.slick-prev{
			// order: 1;
			&:before{
				transform: rotate(-135deg);

			}
			
		}
		.slick-arrow.slick-next{
			// order: 3;
			&:before{
				transform: rotate(45deg);
				
			}
		}
		.slick-arrow.slick-disabled{
			border-color: #333;
		}
	}
	
	/* Точки (булиты) */
	&:not(.dots){
		.slick-dots{
			display: none !important;
		}
	}
	&.dots{
		.slick-dots{
			order: 2;
			// flex: auto;
			margin: 50px 0;
			position: relative;
			display: flex;
			align-items: center;
			justify-content: center;
			padding: 0;
			flex-wrap: wrap;
		}
		.slick-dots li{
			list-style: none;
			display: flex;
			justify-content: center;
			align-items: center;
		}
		/* Активная точка */
		.slick-dots li.slick-active{
		}
		/* Элемент точки */
		.slick-dots li button{
			cursor: pointer;
			border: none;
			padding: 0;
			margin: 0 7px;
			font-size: 0;
			background: #999;
			border-radius: 50%;
			width: 16px;
			height: 16px;

			transition: all 0.4s;
			// overflow: hidden;
			// position: relative;
			// &:before{
			// 	content: '';
			// 	position: absolute;
			// 	left: 0;
			// 	top: 0;
			// 	width: 100%;
			// 	height: 100%;
			// 	border-radius: 50%;
			// 	transform: scale(0);
			// 	background: #D9D9D9;
			// 	transition: all 0.4s;
			// }

		}
		.slick-dots li.slick-active button{
			background: $highlightColor;
			// &:before{
			// 	background: $highlightColor;
			// 	transform: scale(1);
			// }
		}
	}	
}